@import url(https://fonts.googleapis.com/css?family=Sriracha&display=swap);
body{background-image:-webkit-gradient(linear, left bottom, right top, from(#3d6db5), color-stop(#0080b5), color-stop(#008b91), color-stop(#00905a), to(#6d8c26));background-image:linear-gradient(to right top, #3d6db5, #0080b5, #008b91, #00905a, #6d8c26);background-repeat:no-repeat;background-attachment:fixed;text-align:center}

.container{box-shadow:-2px 13px 171px 41px rgba(0,0,0,0.6);border-radius:10px 10px 10px 10px;-moz-border-radius:10px 10px 10px 10px;-webkit-border-radius:10px 10px 10px 10px;border:1px solid #000000;width:50%;min-height:500px;margin-top:10%;font-size:24px;margin-left:auto;margin-right:auto;background-color:lightblue}.container .flag{width:200px}.info_container{margin-top:3%;margin-left:5%;text-align:left;float:left;max-width:40%;flex-wrap:wrap}.flag_container{margin-top:3%}h1{font-family:'Sriracha', cursive}

